      height: 200px;
      width: 300px; }
      .header .header-frame div {
        max-width: 800px;
        margin: auto;
        padding: 0 1rem; }
        .header .header-frame div h1 {
          margin: 1rem;
          font-size: 50px;
          font-weight: 200; }
        .header .header-frame div h4 {
          font-size: 20px;
          text-transform: uppercase; } }

@media (max-width: 470px) {
  .header {
    min-height: 200px; }
    .header .header-frame {
      height: 150px;
      width: 250px; }
      .header .header-frame div {
        max-width: 800px;
        margin: auto;
        padding: 0 1rem; }
        .header .header-frame div h1 {
