  .project p {
    font-size: 0.8rem;
    text-align: start; }
  .project a {
    display: block;
    text-decoration: none;
    color: #333;
    padding-bottom: 0.3rem; }
  .project a:hover {
    cursor: pointer;
    font-weight: 400; }

@media (max-width: 890px) {
  .project {
    margin: auto; } }

.blogs-section {
  background: #f2f2f2;
  padding: 1rem 0 2rem; }
  .blogs-section h2 {
    margin-top: 0;
    padding-top: 1rem;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    font-weight: 100;
    font-size: 40px; }
  .blogs-section .blog-wrapper {
    display: flex;
    justify-content: space-between; }
    .blogs-section .blog-wrapper img {
      width: 250px;
      height: 180px;
      padding: 0.2rem;
      border: 1px solid rgba(0, 0, 0, 0.3); }
    .blogs-section .blog-wrapper h4 {
